import {useSelector} from "react-redux";
import Web3 from "web3";
import cron_abi from "../abis/CoineusFuseCron.json";
import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";
import {
    Button,
    Grid,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    ListSubheader,
    Paper,
    Typography
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import CoineusLoading from "../components/CoineusLoading";

export default function CoineusFuseCron(props) {

    const {wallet, rpc} = useSelector(state => state.coineus);
    const {contract_address, description} = props;

    const web3 = new Web3(rpc.fuse);
    const cron_contract = new web3.eth.Contract(cron_abi, contract_address);

    const [pending, setPending] = useState(false);
    const [cronInfo, setCronInfo] = useState(null);

    useEffect(() => {
        getInfo();
        const interval1 = setInterval(() => {
            getInfo();
        }, 10000);
        return () => clearInterval(interval1);
    }, []);

    const getInfo = () => {
        const calls = [
            cron_contract.methods._lastProcessTime().call,
            cron_contract.methods._timeBetweenProcess().call,
            cron_contract.methods.consensusAddress().call,
            cron_contract.methods.contractDelegatedAmount().call,
            cron_contract.methods.getProcessAmount().call,
            cron_contract.methods.validatorAddress().call,
            cron_contract.methods.getTopDeposits().call,
        ]

        makeBatchRequest(web3, calls, "CoineusFuseCron").then(resp => {

            let [
                lastProcessTime,
                timeBetweenProcess,
                consensusAddress,
                contractDelegatedAmount,
                processAmount,
                validatorAddress,
                topDeposits
            ] = resp;

            let td = [...topDeposits];

            td.sort((a,b) => {
                if(parseInt(a.amount) < parseInt(b.amount)){
                    return 1
                }
                if(parseInt(a.amount) > parseInt(b.amount)){
                    return -1
                }
                return 0;
            })

            console.log(td)

            contractDelegatedAmount = contractDelegatedAmount / (10 ** 18);
            processAmount = processAmount / (10 ** 18);

            lastProcessTime = lastProcessTime / 1;
            timeBetweenProcess = timeBetweenProcess / 1;

            setCronInfo({
                lastProcessTime,
                timeBetweenProcess,
                consensusAddress,
                contractDelegatedAmount,
                processAmount,
                validatorAddress,
                topDeposits: td.slice(0,10)
            });
        });
    }

    const [drawingTime, setDrawingTime] = useState(undefined);
    useEffect(() => {

        if (!cronInfo) {
            return;
        }

        setDrawingTime(msToHMS(timeToNextDraw(cronInfo.lastProcessTime + cronInfo.timeBetweenProcess)));
        const interval1 = setInterval(() => {
            setDrawingTime(msToHMS(timeToNextDraw(cronInfo.lastProcessTime + cronInfo.timeBetweenProcess)));
        }, 1000);
        return () => clearInterval(interval1);
    }, [cronInfo]);

    function timeToNextDraw(d) {

        return (d * 1000) > new Date() ? (d * 1000) - new Date() : 0;
    }

    function msToHMS(ms) {
        let h = ms / 3.6e6 | 0;
        let m = (ms % 3.6e6) / 6e4 | 0;
        let s = (ms % 6e4) / 1e3 | 0;
        return `${h}:${('' + m).padStart(2, '0')}:${('' + s).padStart(2, '0')}`;
    }

    const process = async () => {
        if (!wallet.address) return;

        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(cron_abi, contract_address);
        const gasPrice = await web3.eth.getGasPrice();

        contract
            .methods
            .process()
            .send({from: wallet.address, gas: 1000000, gasPrice})
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt, a, b) {
                setPending(false);
            });

    }

    const deposit = (amount) => {

        const web3 = new Web3(window.provider);
        const cron_contract = new web3.eth.Contract(cron_abi, contract_address);

        cron_contract.methods.deposit().send(
            {
                from: wallet.address,
                value: web3.utils.toWei(amount),
                gas: 10000000
            }).on('error', function (error) {
            setPending(false)
        })
            .then(function () {
                getInfo();
                setPending(false);
            });

    }

    if (!cronInfo) return (<></>);

    return (
        <Grid item xs={12}>
            <Paper>
                <List>

                    <ListItem>
                        <ListItemText
                            primary={CoineusCryptoFormat(cronInfo.contractDelegatedAmount)}
                            primaryTypographyProps={{fontWeight: 700, textAlign: 'center'}}
                            secondary="Staked FUSE"
                            secondaryTypographyProps={{textAlign: 'center'}}
                        />
                        <ListItemText
                            primary={CoineusCryptoFormat(cronInfo.processAmount)}
                            primaryTypographyProps={{fontWeight: 700, textAlign: 'center'}}
                            secondary="FUSE to Process"
                            secondaryTypographyProps={{textAlign: 'center'}}
                        />
                        <ListItemText
                            primary={drawingTime}
                            primaryTypographyProps={{fontWeight: 700, textAlign: 'center'}}
                            secondary="Until Next Process"
                            secondaryTypographyProps={{textAlign: 'center'}}
                        />
                    </ListItem>

                    <ListItem>
                        <ListItemText
                            primary={description}
                            primaryTypographyProps={{textAlign: 'center'}}
                        />
                    </ListItem>

                    <ListItemButton>
                        <Button
                            fullWidth
                            onClick={process}
                            disabled={((cronInfo.lastProcessTime + cronInfo.timeBetweenProcess) * 1000) > (new Date()).getTime()}
                        >Process</Button>
                    </ListItemButton>

                </List>

                <Grid container spacing={2} alignItems="center" justifyContent="center">
                    <Grid item>
                        <Typography>Donate FUSE</Typography>
                    </Grid>
                    <Grid item>
                        <Button
                            variant="contained"
                            onClick={() => deposit('10')}
                        >10</Button>

                    </Grid>
                    <Grid item>
                        <Button
                            variant="contained"
                            onClick={() => deposit('100')}
                        >100</Button>

                    </Grid>
                    <Grid item>
                        <Button
                            variant="contained"
                            onClick={() => deposit('1000')}
                        >1000</Button>

                    </Grid>
                </Grid>

                <List>
                    <ListSubheader>Top Donors</ListSubheader>
                    {
                        cronInfo.topDeposits.map( td => {
                            return <ListItem key={td.sender}>
                                <ListItemText
                                    primary={addressMap(td.sender)}
                                    primaryTypographyProps={{variant: 'body2'}}
                                />
                                <ListItemText
                                    primary={CoineusCryptoFormat(td.amount / 10**18)}
                                    primaryTypographyProps={{variant: 'body2', textAlign: 'right'}}
                                />
                            </ListItem>
                        })
                    }
                </List>
            </Paper>
            <CoineusLoading open={pending} label="Transaction Pending"/>
        </Grid>
    )
}

function addressMap (address) {

    //sikk 0x024BFD2647945c0DB4416545e2eA5F30C0bcf741
    switch (address) {
        case '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe':
            return 'Coineus Fund';
        case '0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2':
            return 'Coineus Validator';
        default:
            return "0x..." + address.substr(-6)
    }
}